$bg-color: #EDF2EC;
$nav-color: #010201;
$orange-color: #E06733;
$green-color: #499A18;
$light-grey-color: #717171;
$dark-grey-color: #636060;
$border-color: #E3E1D5;


* {
    box-sizing: border-box;
    margin: 0;
    padding: 0; }

html {
    scroll-behavior: smooth; }
a {
    color: black;
    text-decoration: none;
    &:hover {
        color: $orange-color; } }
li {
    list-style-type: none; }


.green {
    color: $green-color; }

.container {
    position: relative;
    max-width: 1222px;
    margin: 0 auto; }

.button {
    transition: 0.7s;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid $orange-color;
    border-radius: 5px;
    background-color: transparent;
    width: 161px;
    height: 50px;
    &:hover {
        background-color: $orange-color;
        color: white; } }


.header {
    font-family: 'Inika', serif;
    background-color: $bg-color;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 134px;
    &__navbar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        width: 1220px; }
    &__logo {
        display: flex;
        flex-direction: row;
        align-items: flex-end; }
    &__nav-list {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 452px; }
    &__logo-text {
        font-size: 16px;
        line-height: 11px; } }
.nav-menu {
    &__list-item {
        font-size: 16px;
        a {
            color: $nav-color;
            &:hover {
                font-weight: 700;
                color: $orange-color; } } } }


.welcome {
    font-family: 'Inter', sans-serif;
    background: $bg-color;
    min-height: 532px;
    padding-top: 62px;
    &__content-container {
        display: flex;
        justify-content: space-between;
        align-items: center; }
    &__text-container {
        max-width: 549px; }
    &__header {
        font-weight: 700;
        font-size: 45px;
        line-height: 50px;
        color: $light-grey-color;
        margin-bottom: 46px; }
    &__text {
        max-width: 525.44px;
        color: $dark-grey-color;
        font-size: 20px;
        line-height: 35px;
        margin-bottom: 47px; }
    &__button {
        border: 2px solid $orange-color;
        border-radius: 10px;
        font-size: 16px;
        font-family: 'Inter', sans-serif;
        width: 158px;
        height: 44px; } }

.about {
    font-family: 'Inika', serif;
    min-height: 666px;
    padding-top: 93px;
    &__content-container {
        display: flex;
        justify-content: space-between;
        align-items: center; }
    &__text-container {
        max-width: 605px; }
    &__header {
        font-weight: 400;
        max-width: 463px;
        font-size: 40px;
        color: $orange-color;
        margin-bottom: 40px; }
    &__text {
        font-family: 'Inter', sans-serif;
        line-height: 30px;
        font-size: 20px;
        color: $dark-grey-color; } }

.service {
    background-color: $bg-color;
    min-height: 1300px;
    padding-top: 40px;
    &__header-bar {
        font-family: 'Inika', serif;
        max-width: 1007px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 80px; }
    &__header {
        font-weight: 400;
        font-size: 40px;
        width: 306px;
        color: $green-color; }
    &__buttons-bar {
        display: flex;
        justify-content: space-between;
        width: 559px; }
    &__button {
        font-family: 'Inika', serif;
        color: $orange-color;
        font-size: 20px; }
    &__services {
        font-family: 'Inter', sans-serif;
        min-height: 955px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-content: space-between; }
    &__service-card {
        width: 330px;
        transition: 1s all;
        &_blured {
            filter: blur(2px);
            transition: 1s all; } }
    &__text-container {
        padding-top: 10px;
        width: 330px;
        height: 100px;
        display: flex;
        flex-direction: column;
        align-items: center;
        border: 1px solid $border-color;
        border-radius: 0px 0px 20px 20px; }
    &__card-img {
        display: block;
        width: 330px;
        height: 350px;
        object-fit: cover;
        border-radius: 20px 20px 0px 0px; }
    &__card-header {
        color: $orange-color;
        font-size: 20px;
        margin-bottom: 10px; }
    &__card-text {
        color: $light-grey-color;
        font-size: 16px; } }
.prices {
    font-family: 'Inter', sans-serif;
    padding-top: 40px;
    min-height: 560px;
    &__content-container {
        height: 500px;
        display: flex;
        flex-flow: column wrap;
        align-content: space-between; }
    &__leaves-img {
        display: flex;
        align-self: flex-end; }
    &__price-box {
        border: 1px solid $border-color;
        border-radius: 20px;
        width: 332px;
        min-height: 282px;
        display: flex;
        justify-content: center;
        padding: 34px 0 34px 0; }
    &__item-box {
        width: 250px;
        min-height: 214px; }
    &__item {
        padding: 16px 40px;
        background-color: $bg-color;
        border-radius: 20px;
        height: 50px;
        margin-bottom: 32px;
        &:last-child {
            margin-bottom: 0px; } }
    &__item-content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 18px;
        p {
            font-size: 20px;
            font-weight: 700; }
        img {
            cursor: pointer; } }
    &__header1 {
        font-size: 40px;
        line-height: 48px;
        margin-bottom: 60px; }
    &__header2 {
        text-align: right;
        font-size: 40px;
        line-height: 50px;
        margin-bottom: 38px; }
    &__text-container {
        display: flex;
        flex-flow: column wrap;
        align-items: flex-end;
        width: 459px;
        margin-bottom: 45px; }
    &__button {
        font-family: 'Inika', serif;
        color: $orange-color;
        font-size: 20px; } }

.price-info {
    &_active {
        height: 154px;
        background: #D6E7D2; }
    &__content {
        display: none; }
    &__content_active {
        display: block; }
    &__line {
        margin-top: 10px;
        margin-bottom: 6px;
        height: 0.5px;
        background: #AEA1A1; }
    &__text {
        font-size: 10px;
        color: $light-grey-color;
        font-weight: 300;
        line-height: 10px;
        margin-bottom: 6px; }
    &__price {
        font-size: 12px;
        margin-bottom: 12px;
        span {
            color: $orange-color;
            font-weight: 700; } }
    &__button {
        width: 86px;
        height: 25px;
        background: #FFFFFF;
        border: none;
        cursor: pointer;
        border-radius: 10px;
        font-size: 14px;
        font-weight: 700;
        color: $light-grey-color; } }

.contacts {
    min-height: 550px;
    background-color: $bg-color;
    padding-top: 53px;
    &__content-container {
        img {
            position: absolute; } }
    &__city-selector {
        position: absolute;
        top: 95px;
        right: 0; }
    &__dropdown {
        position: relative;
        &::before {
            content: "";
            position: absolute;
            top: 50%;
            right: 35px;
            transform: translateY(-50%);
            width: 16px;
            height: 16px;
            display: block;
            background: url('../icons/accordion_btn-dark.svg') no-repeat right;
            pointer-events: none; } }
    &__dropdown-menu {
        display: none;
        width: 448px;
        height: 214px;
        margin-top: 47px;
        padding: 17px 33px 0 33px;
        background: transparent;
        border: 1px solid #000000;
        border-radius: 20px;
        &_active {
            display: block; } }
    &__info-city {
        display: flex;
        flex-direction: column;
        th {
            text-align: left;
            font-weight: 400;
            font-family: 'Inter';
            line-height: 40px;
            font-size: 16px;
            letter-spacing: 0.8px;
            span {
                color: $light-grey-color; } } }
    &__info-text {
        margin-bottom: 18px; }
    &__call-button {
        align-self: center;
        width: 122px;
        height: 26px;
        border: 1px solid #E06733;
        border-radius: 5px;
        background: transparent;
        cursor: pointer;
        font-family: 'Inika';
        font-weight: 700;
        font-size: 10px;
        line-height: 13px;
        color: $light-grey-color; }
    &__select {
        //reset
        outline: none;
        -moz-appearance: none;
        -webkit-appearance: none;
        appearance: none;
        border: none;
        //styles
        width: 450px;
        height: 50px;
        padding: 5px 35px 5px 20px;
        background-color: #D6E7D2;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        cursor: pointer;
        font-family: 'Inter', sans-serif;
        font-size: 20px;
        line-height: 40px;
        letter-spacing: 1px;
        color: $light-grey-color;
        &_active {
            background: #C1E698; } }
    &__header {
        font-family: 'Inika', serif;
        color: $green-color;
        font-size: 40px;
        position: absolute;
        top: 7px;
        right: 50%;
        transform: translate(50%, 0); } }

.footer {
    font-family: 'Inter', sans-serif;
    min-height: 130px;
    &__content-container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        min-height: 130px; }
    &__data {
        font-size: 20px;
        line-height: 50px;
        word-spacing: 20px;
        display: flex; }
    &__name {
        font-size: 20px;
        line-height: 50px; }
    &__social-list {
        height: 50px;
        display: flex;
        align-items: center;
        li {
            height: 32px;
            margin-right: 20px;
            &:last-child {
                margin-right: 0; } } } }
